import React from 'react';
import Navigation from './Navigation';
import underconstruction from './underconstruction.jpg';

class UnderConstruction extends React.Component {
	render() {
		return(
			<div>
			
				<Navigation onMainRouteChange={this.props.onMainRouteChange}/>
				<div className='mt4 tc w-100' style={{position: 'fixed', fontFamily: 'verdana'}}>
					<p className='f4 fw4 dib'>Content will be available by September 19th, 2019</p>
				</div>
				<div className='flex items-center justify-center' style={{height: '100vh'}}>
					<img alt='' src={underconstruction} />
				</div>

			</div>
		);
	}
}

export default UnderConstruction;