import React from 'react';

const Navigation = ({onMainRouteChange}) => {
	return (
		<div className='w-100 tr' style={{position: 'fixed', fontFamily: 'courier', zIndex: '10'}}>
			<p 
				className='mt4 mr4 dib f3 underline pointer dim'
				onClick={() => onMainRouteChange('Main')}>Home</p>
		</div>
	);
}

export default Navigation;